@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .input {
        @apply border-b-2 w-full pb-1 pl-1 border-[#bdbdbd] focus-visible:outline-none focus-visible:border-primary
    }
    .button {
        @apply bg-primary hover:bg-primaryDark p-2 px-4 rounded-md text-white duration-200
    }
}